// Navbar overrides

#nav{
  border: 0px solid transparent;
  margin-bottom: 0px;
  position: relative;
  top: 7rem;
  z-index: 3;
  &.navbar-default{
    background-color: transparent;
  }
  .container-fluid{
    padding-left: 0px;
    padding-right: 0px;
    .navbar-header{
      margin-left: 0px !important;
      margin-right: 0px !important;
      padding: 0rem 1.5rem 0rem 0rem;
      &#ham{
        button.navbar-toggle{
          margin-top: .7rem;
          margin-bottom: .7rem;
          -webkit-transition: all .4s;
          -o-transition: all .4s;
          transition: all .4s;
          &:hover{
            background-color: $magenta;
          }&:focus{
            background-color: $magenta;
          }
          span.icon-bar{
            background-color: $white;
          }
        }
      }
      &#brandHeader{
        position: absolute;
        left: 0rem;
        top: -6rem;
        z-index: 5;
        padding: 0 0 0 0;
        .navbar-brand{
          padding: 0 0 0 0;
          #headImg{
            max-width: 225px;
            margin-left: 0.5rem;
          }
        }
      }
    }
    #navbar-collapse{
      margin: 0 0 0 0;
      padding: 5rem 0 0 0;
      ul.navbar-nav{
        padding-top: 3rem;
        padding-left: 2rem;
        max-width: 100%;
        margin: 0 0 0 0;
        font-size: 1.8rem;
        li{
          margin-bottom: 1rem;
          font-size: inherit;
          a {
            color: $white !important;
            font-size: inherit;
            padding-left: 5rem;
            -webkit-transition: all .4s;
            -o-transition: all .4s;
            transition: all .4s;
            &:focus{
              background-color: inherit;
              color: rgba(255,255,255, 0.7) !important;
            }
            &:hover{
              color: rgba(255,255,255, 0.7) !important;
              background-color: inherit;
            }
          }
          &.dropdown{
            &.open{
              background-color: inherit;
              .dropdown-toggle{
                background-color: inherit;
                margin-bottom: 0.5rem;
              }
            }
            .dropdown-toggle{
              font-size: inherit;
              .caret{
                border: none;
                display: none;
              }
              &::after{
                font: normal normal normal 20px/1 FontAwesome;
                content: "\f078"!important; /* the desired FontAwesome icon */
                vertical-align: 0; /* to center vertically */
                font-size: inherit;
              }
            }
            ul.dropdown-menu{
              background-color: $magenta;
              top: 100%;
              max-width: 350px;
              border: none;
              border-radius: 0px;
              font-size: inherit;
              padding: 1rem;
              li{
                list-style-type: none !important;
                font-size: inherit;
                a{
                  font-size: inherit;
                  -webkit-transition: all .4s;
                  -o-transition: all .4s;
                  transition: all .4s;
                  &:hover{
                    color: rgba(255, 255, 255, 0.7);
                    background-color: inherit;
                  }
                  &:focus{
                    color: rgba(255, 255, 255, 0.7);
                    background-color: inherit;
                  }
                }
                &.dropdown-submenu{
                  position: relative;
                  a.dropdown-submenu-toggle{
                    font-size: inherit;
                    margin-bottom: 0.5rem;
                    -webkit-transition: all .4s;
                    -o-transition: all .4s;
                    transition: all .4s;
                    &:hover{
                      color: rgba(255, 255, 255, 0.7);
                      background-color: inherit;
                      &::after{
                        color: rgba(255, 255, 255, 0.7);
                      }
                    }
                    .caret{
                      border: none;
                    }
                    &::after{
                      font: normal normal normal 20px/1 FontAwesome;
                      content: "\f105"!important; /* the desired FontAwesome icon */
                      vertical-align: 0; /* to center vertically */
                      font-size: inherit;
                      color: $white;
                    }
                  }
                  .dropdown-menu {
                    padding: 1rem; 
                    background-color: $coral;
                    max-width: 350px;
                    font-size: inherit;
                    li{
                      font-size: inherit;
                      &.subNav{
                        a{
                          font-size: 1.5rem;
                          margin-left: 1rem;
                        }
                      }
                      &.first {
                        a{
                          font-size: inherit;
                        }
                      }
                      a{
                        color: $white;
                        -webkit-transition: all .4s;
                        -o-transition: all .4s;
                        transition: all .4s;
                        &:hover{
                          color: rgba(255, 255, 255, 0.7);
                          background-color: inherit;
                        }
                      }
                    }
                    &.on{
                      display: block;
                    }
                  }
                }
                
              }
            }
          }
        }
      }
    }
  }
}

@media (min-width: 450px){

  #nav{
    .container-fluid{
      .navbar-header{
        &#ham{
          .navbar-toggle{
            margin-right: 2.5rem;
          }
        }
      }
    }
  }

}

@media (max-width: 768px) {

  #nav{
    .container-fluid{
      #navbar-collapse{
        ul.navbar-nav{
          li{
            a {
              padding: 0 0;
              padding-left: 0;
            }
          }
        }
      }
    }
  }

}

@media (min-width: 768px) {

  #nav{
    top: 3rem;
    .container-fluid{
      .navbar-header{
        padding: 0 0 0 0;
        &#brandHeader{
          left: 0.5rem;
          top: -2rem;
          .navbar-brand{
            #headImg{
              margin-left: 1.5rem;
            }
          }
        }
      }
      #navbar-collapse{
        width: 71%;
        padding: 0 0 0 0;
        ul.navbar-nav{
          font-size: 1.4rem;
          padding-top: 0;
          padding-left: 0;
          width: 100%;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-orient: horizontal;
          -webkit-box-direction: normal;
              -ms-flex-flow: row;
                  flex-flow: row;
          -webkit-box-pack: space-evenly;
              -ms-flex-pack: space-evenly;
                  justify-content: space-evenly;
          li{
            margin-bottom: 0;
            padding: 0 0 0 0;
            a {
              padding-right: 0rem;
              padding-left: 0rem;
              -webkit-transition: all .4s;
              -o-transition: all .4s;
              transition: all .4s;
              &:hover{
                color: rgba(255,255,255, 0.7) !important;
                background-color: inherit;
              }
            }
            &.dropdown{
              .dropdown-toggle{
                background-color: inherit;
                font-size: inherit;
                margin-bottom: 0 !important;
                .caret{
                  border: none;
                  display: none;
                }
              }
              ul.dropdown-menu{
                background-color: rgba(152, 38, 88, .89);
                box-shadow: 0px 3px 6px #00000029;
                top: 98%;
                max-width: 200px;
                li{
                  padding: 0.5rem;
                  a{
                    white-space: unset;
                    background-color: inherit;
                    -webkit-transition: all .4s;
                    -o-transition: all .4s;
                    transition: all .4s;
                    &:hover{
                      color: rgba(255,255,255, 0.7) !important;
                      background-color: inherit;
                    }
                  }
                  &.dropdown-submenu{
                    .dropdown-menu {
                      top:0; 
                      left:100%; 
                      margin-top:-6px; 
                      margin-left:-1px;
                      margin-bottom: 8px;
                      background-color: rgba(191,54,46, 0.89) ;
                      max-width: unset;
                      box-shadow: 0px 3px 6px #00000029;
                      li{
                        &.subNav{
                          a{
                            font-size: 1.2rem;
                            margin-left: 0rem;
                            background-color: inherit;
                            -webkit-transition: all .4s;
                            -o-transition: all .4s;
                            transition: all .4s;
                            &:hover{
                              background-color: inherit;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

@media (min-width:1024px){

  #nav{
    top: 7rem;
    .container-fluid{
      .navbar-header{
        &#ham{
          .navbar-toggle{
            margin-right: 2.5rem;
          }
        }
        &#brandHeader{
          top: -6rem;
          left: 1rem;
          .navbar-brand{
            margin-left: -8px !important;
            #headImg{
              max-width: 250px;
              margin-left: 1rem;
            }
          }
        }
      }
      #navbar-collapse{
        width: 70%;
        ul.navbar-nav{
          font-size: 1.6rem;
          li{
            &.dropdown{
              ul.dropdown-menu{
                &#about{
                  left: 2%;
                }
                &#local{
                  right: 2%;
                }
                li{
                  &.dropdown-submenu{
                    .dropdown-menu.second {
                      margin: 0 0 0 0;
                      left: 105%;
                      &.on{
                        display:flex;
                        flex-flow: row wrap;
                        max-width: 400px;
                        min-width: 300px;
                      }
                      li{
                        &.first{
                          flex: 0 0 100%;
                        }
                        &.subNav{
                          flex: 0 0 50%;
                          a{
                            font-size: 1.3rem;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
  }

}

