// variables
$purple: #532565;
$magenta: #982568;
$red: #981f32;
$coral: #bf362e;
$orange: #e07c3e;
$dark-gray: #373a3c;
$medium-gray: #818a91;
$light-gray: #dddddd;
$white: #fff;
$link-color: #0044B3;

$magentaTint: rgba(245, 232, 239, 0.8);
$coralTint: rgba(246, 225, 224, 0.8);

$gradient: linear-gradient(90deg, $purple 10%, $magenta 30%, $red 50%, $coral 76%, $orange 100%);

$vertGradient: linear-gradient(180deg, $purple 10%, $magenta 30%, $red 50%, $coral 76%, $orange 100%);

$h1-size: 40px;
$h2-size: 28px;
$h3-size: 24px;
$h4-size: 28px;
$h5-size: 20px;
$h6-size: 20px;
$bold: 700;

/* These values correspond with the CSS classes for margin and padding */

$one: 1rem;
$two: 2rem;
$three: 3rem;
$four: 4rem;
$five: 5rem;