// Font overrides
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  color: $dark-gray;
}

body,html{
  margin: 0;
  padding: 0;
  width: 100vw !important;
  height: 100%;
  z-index: 0;
}

html {
  scroll-behavior: smooth;
}

a {
  color: $link-color;
  text-decoration: none !important;
}

h1, h2, h3, h5 {

  font-family: 'Montserrat', sans-serif;
  font-weight: $bold;
}

h1, h2, h4, h5, h6{
 color: $dark-gray;
}

h3 {
  color: $medium-gray;
}

h4, h6 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
}

h1 {
  font-size: $h1-size;
  line-height: 48px;
}

h2 {
  font-size: $h2-size;
  line-height: $h2-size + 8px;
}

h3 {
