//BUTTON STYLES + HOVER EFFECTS

.btn {
  color: $white;
  font-weight: bold;
  border: none !important;
  padding: 1rem;
  font-size: 1.5rem;
  white-space: wrap;
  -webkit-transition: all .4s; /* Safari prior 6.1 */
  -o-transition: all .4s;
  transition: all .4s;
  &:hover{
    opacity: .8;
    color: #fff;
    border-radius: 5rem;
    border: none !important;
  }
  &:focus{
    color: #fff;
    border: none !important;
  }
}



.accChevron{
  -ms-flex-item-align: center;
  -ms-grid-row-align: center;
  align-self: center;  
  transform: rotate(0deg);
  transition: transform .3s ease-in;
}
.rotate-chevron {
  transform: rotate(180deg);
}

.panel-group {
  // box-shadow: 0 3px 5px $light-gray;
  .panel-default{
    margin-top: 0;
    margin-bottom: 1rem;
    .panel-heading {
      border: 1px solid $light-gray;
      // border: 1px solid $purple;
      background-color: $white;
      cursor: pointer;
      .panel-title {
        font-weight: bold;
        display: flex;
        flex-flow: row;
        a{
          flex: 0 0 95%;
        }
        i{
          flex: 0 0 5%;
        }
      }
    }
    .panel-body{
      ul.special{
        padding-left: 0;
        max-width: 97%;
        margin-left: auto;
        margin-right: auto;
        li{
          list-style-type: none !important;
        }
      }
    }
    &.panel + .panel {
      margin-top: 0;
      .panel-body{
        padding: 2rem 3rem;
      }
    }
  } 
}

//READ MORE STYLES

.read-more{
  background-color: transparent;
  border: transparent;
  padding: 0 0 0 0;
  margin: 0 0 1.5rem 0;
  opacity: 1;
  cursor: pointer;
  -webkit-transition: .2s .3s ease-in;
  -o-transition: .2s .3s ease-in;
  transition: .2s .3s ease-in;
  &:hover{
    text-decoration: underline;
  }
  &.readMoreOff {
    opacity:0;
  }
}
.cs-word-hidden{
  display: none;
  max-width: 100%;
}
.read-less {
  background-color: transparent;
  border: transparent;
  padding: 0 0 0 0;
  margin: 0 0 0 0;
  cursor: pointer;
  &:hover{
    text-decoration: underline;
  }
}


