.footer{
  background-color: $purple;
  color: $white;
  padding: 3rem 2rem;
  position: relative;
  top: -22rem;
  margin-bottom: -25rem;
  .row{
    &#arrowRow{
      display: block;
      #upArrow{
        font-size: 3rem;
        padding: 1rem 1.3rem;
        position: relative;
        top: -6.5rem;
        cursor: pointer;
        .fa-chevron-up{
          opacity: .8;
          -webkit-transition: all .4s; /* Safari prior 6.1 */
          -o-transition: all .4s;
          transition: all .4s;
        }
        &:hover{
          .fa-chevron-up{
            opacity: 1;
          }
        }
      }
    }
  }
  .container{
    position: relative;
    top: -5rem;
    .row{
      .col-xs-12{
        .row{
          &.special{
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-orient: vertical;
            -webkit-box-direction: normal;
                -ms-flex-flow: column;
                    flex-flow: column;
            -webkit-box-pack: center;
                -ms-flex-pack: center;
                    justify-content: center;
            -webkit-box-align: center;
                -ms-flex-align: center;
                    align-items: center;
          }
          .col-xs-12, .col-xs-6{
            a {
              font-size: inherit;
              color: $white;
              -webkit-transition: all .4s; /* Safari prior 6.1 */
              -o-transition: all .4s;
              transition: all .4s;
              &:hover{
                opacity: .8;
              }
            }
            p{
              font-size: inherit;
              color: $white;
              opacity: inherit;
              &.footText{
                font-size: 1.5rem;
                &#nih{
                  font-size: 1rem;
                }
              }
              &.lg{
                font-size: 2.2rem;
              }
              &.med{
                font-size: 1.8rem;
              }
              &.footOrng{
                color: #E48E58;
              }
            }

            #footImg{
              cursor: pointer;
              max-width: 100%;
              -webkit-transition: all .4s; /* Safari prior 6.1 */
              -o-transition: all .4s;
              transition: all .4s;
              &:hover{
                opacity: .8;
              }
            }
            .row{
              .col-xs-12{
                p{
                  font-size: inherit;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media (min-width: 768px) {
  .footer{
    top: -1px;
    .container{
      .row{
        .col-xs-12{
          .row{
            &.special{
              flex-flow: row wrap;
            }
            .col-xs-12, .col-xs-6{
              #footImg{
                max-width: 80%;
              }
              a, p {
                &.footText{
                  font-size: 1.3rem;
                }
                &.med{
                  font-size: 1.6rem;
                }
              }
            }
            .col-md-6{
              max-width: 50%;
            }
          }
        }
      }
    }
  }
}

@media (min-width: 1024px) {
  .footer{
    .container{
      position: static;
      .row{
        .col-xs-12{
          .row{
            .col-xs-12, .col-xs-6{
              a, p {
                &.footText{
                  &#nih{
                    max-width: 82%;
                  }
                }
              }
            }
            .col-lg-12{
              max-width: 100%;
            }
          }
          &.col-lg-2.pad{
            padding: 0 1rem;
          }
        }
      }
    }
  }
}