// Homepage styles

#covid{
  background-color: $white !important;
  opacity: 0.6 !important;
  position: relative;
  bottom: 0;
  top: 15rem;
  .container{
    a{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-flow: row wrap;
              flex-flow: row wrap;
      -webkit-box-align: center;
      -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: start;
      -ms-flex-pack: start;
              justify-content: flex-start;
      margin-bottom: 0;
      font-size: 2.3rem;
      text-align: left;
      width: 100% !important;
      color: #FF0e00 !important;
      &:hover{
        color: #FF0e00 !important;
      }
      .bold{
        -ms-flex-item-align: center;
        -ms-grid-row-align: center;
        align-self: center;
        display: inline-block;
        font-size: inherit;
      }
      .tagLine{
        display: none;
        font-size: inherit;
      }
    }
  }
}

#homepageTopJumbo{
  background: $purple;
  z-index: 1;
  position: relative;
  top: -75rem;
  padding: 77rem 3rem 20rem 3rem;
  .container-fluid{
    #topImg{
      margin: 0 auto;
      display: none;
    }
    #homepageH1{
      max-width: 100%;
      position: relative;
      font-size: 4rem;
      text-align: left;
      left: -5rem;
      top: 10rem;
      margin: 0rem 3rem;
      .jumbonowrap {
        display:none;
      }
      .jumbowrap {
        display:inline-block;
      }
    }
  }
}

#homepageTopText{
  position: relative;
  top: -65rem;
}

//homepage who we are overlays

#homepageWho{
  position: relative;
  top: -62rem;
  .row{
    &#covid-content{
      margin-top: 3rem;
    }
    .photoRow{
      a{
        .imgContainer {
          position: relative;
          width: 100%;
          .image {
            display: block;
            width: 100%;
            height: auto;
          }
          .overlay {
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            height: 100%;
            width: 100%;
            opacity: 1;
            -webkit-transition: all .4s; /* Safari prior 6.1 */
            -o-transition: all .4s;
            transition: all .4s;
            background: rgba(255,255,255,.65);
            .imgText {
              position: absolute;
              top: 50%;
              left: 50%;
              width: 75%;
              -webkit-transform: translate(-50%, -50%);
              -ms-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
              text-align: center;
              h2{
                text-transform: uppercase;
                font-size: 24px;
              }
            }
          }
        } 
        &:hover{
          .imgContainer{
            .overlay{
              background: rgba(255,255,255,.8);
            }
          }
        }
      }
    }
  }
}

// Carousel overrides

//homepage Quote Jumbo

#homepageQuote{
  padding: 8rem 0rem;
  position: relative;
  top: -56rem;
  margin-bottom: -34rem;
  background: $gradient;
  border-bottom: 0px solid transparent;
  .container-fluid{
    #homepageQuoteRow{
      .row{
        #quoteCarousel{
          #quote-carousel{
            padding: 0 10px 30px 10px;
            margin-top: 30px 0px 0px;
            #homepage-carousel-inner {
              background: white;
              border-radius: 24px;
              opacity: 0.79;
              min-height: 350px;
              .item{
                height: inherit;
                blockquote{
                  border-left: none; 
                  margin: 0;
                  p {
                    color: $purple;
                    opacity: 1.0;
                    font-weight: $bold;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}

#quoteImg{
  display: none;
}

/* carousel */

/* Control buttons  */
#quote-carousel .carousel-control{
  background: none;
  color: #222;
  font-size: 2.3em;
  text-shadow: none;
  margin-top: 30px;
}

/* Previous button  */
#quote-carousel .carousel-control.left {
  left: -12px;
}

/* Next button  */
#quote-carousel .carousel-control.right {
  right: -12px !important;
}

/* Changes the position of the indicators */
#quote-carousel .carousel-indicators {
  right: 50%;
  top: auto;
  bottom: 0px;
  margin-right: -19px;
}

/* Changes the color of the indicators */
#quote-carousel .carousel-indicators li {
  // background: #c0c0c0;
  background: rgba(255,255,255,.79);
  border: 1px solid $dark-gray;
}

#quote-carousel .carousel-indicators .active {
  background: #333333;
}

#quote-carousel img{
  width: 250px;
  height: 100px
}

.item blockquote img {
  margin-bottom: 10px;
}

/* End carousel */

@media (max-width: 768px) {
  /* Make the indicators larger for easier clicking with fingers/thumb on mobile */
  
    #quote-carousel .carousel-indicators {
      bottom: -20px !important;  
  }

  #quote-carousel .carousel-indicators li {
      display: inline-block;
      margin: 0px 5px;
      width: 15px;
      height: 15px;
  }

  #quote-carousel .carousel-indicators li.active {
      margin: 0px 5px;
      width: 20px;
      height: 20px;
  }
}

